import React, {useRef, useState} from 'react'
import './VisitkaPage.css'
import visitkaSVG from  '../svg/visitkaSVG.svg'
import visitkaSVGGold from  '../svg/visitkaSVG-gold.svg'
import visitkaSVGMirror from  '../svg/visitkaSVG-mirror.svg'
import visitkaSVGRed from  '../svg/visitkaSVG-red.svg'

import instagramSVG from  '../svg/instagramSVG.svg'
import instagramSVGGold from  '../svg/instagramSVG-gold.svg'
import instagramSVGMirror from  '../svg/instagramSVG-mirror.svg'
import instagramSVGRed from  '../svg/instagramSVG-red.svg'


import otskaniryimenya from  '../svg/otskaniryimenya.svg'
import podpiska10 from  '../svg/podpiska10.svg'
import navedite from  '../svg/navedite.svg'



import nastoikeSVGMirror from '../svg/nastoikeSVG-mirror.svg'
import nastoikeSVGGold from '../svg/nastoikeSVG-gold.svg'
import nastoikeSVGRed from '../svg/nastoikeSVG-red.svg'
import nastoikeSVG from '../svg/nastoikeSVG.svg'



export const VisitkaPage = ()=>{

    const [instagram, setInstagram] = useState('instagram'.toUpperCase())
    const instaLOGO = useRef();
    const instaTEXT = useRef();
    const visitkaBorder = useRef();
    const nastoikeBorder = useRef();

    const instaLOGO_0 = useRef();
    const instaTEXT_0 = useRef();

    const [fSize, setFSize] = useState(60)

    const [visitkaBorderColor, setVisitkaBorderColor] = useState(visitkaSVG)
    const [instagramSVGColor, setInstagramSVG] = useState(instagramSVG)
    const [footerText, setFooterText] = useState(otskaniryimenya)

    const [textlen, setTextLen] = useState(0)

    const [ textColor, setTextColor] = useState()


    const [nastoikeBorderColor, setNastoikeBorderColor] = useState(nastoikeSVG)
    //const [instagramSVGColor, setInstagramSVG] = useState(instagramSVG)
   // const [footerText, setFooterText] = useState(otskaniryimenya)

    const changeInstagramHandler = (e) =>{


        setInstagram(e.target.value.toUpperCase())


        console.log(textlen > e.target.value.length )




        if(instaTEXT.current.offsetWidth+instaLOGO.current.offsetWidth>415){
            //    console.log(instaTEXT.current )
            //   console.log(fSize)
            instaTEXT.current.style.fontSize = `${fSize-5}px`
            instaLOGO.current.style.width = `${fSize-5}px`
            instaTEXT_0.current.style.fontSize = `${fSize-5}px`
            instaLOGO_0.current.style.width = `${fSize-5}px`
            setFSize(fSize-5)
        }
        if((textlen >  e.target.value.length)&&
            (instaTEXT.current.offsetWidth+instaLOGO.current.offsetWidth<370) &&
            (fSize+5<61)

        ) {
            instaTEXT.current.style.fontSize = `${fSize+5}px`
            instaLOGO.current.style.width = `${fSize+5}px`
            instaTEXT_0.current.style.fontSize = `${fSize+5}px`
            instaLOGO_0.current.style.width = `${fSize+5}px`
            setFSize(fSize+5)
        }


        setTextLen(e.target.value.length)

    }

    const colorChangerBlack = () => {
        setVisitkaBorderColor(visitkaSVG)
        setInstagramSVG(instagramSVG)
        setNastoikeBorderColor(nastoikeSVG)
        setTextColor('')
    }
    const colorChangerGold = () => {
        setVisitkaBorderColor(visitkaSVGGold)
        setInstagramSVG(instagramSVGGold)
        setNastoikeBorderColor(nastoikeSVGGold)
        setTextColor('textGradientGold')
    }
    const colorChangerMirror = () => {
        setVisitkaBorderColor(visitkaSVGMirror)
        setInstagramSVG(instagramSVGMirror)
        setNastoikeBorderColor(nastoikeSVGMirror)
        setTextColor('textGradientSilver')
    }
    const colorChangerRed = () => {
        setVisitkaBorderColor(visitkaSVGRed)
        setInstagramSVG(instagramSVGRed)
        setNastoikeBorderColor(nastoikeSVGRed)

        setTextColor('textRed')
    }


    const textChangerOtskaniryi = () =>{
        setFooterText(otskaniryimenya)
    }

    const textChangerPodpiska10 = () =>{
        setFooterText(podpiska10)
    }

    const textChangerNavedite = () =>{
        setFooterText(navedite)
    }



    return(
        <div>
            <h1>PROMOTIONPLUS.BIZ</h1>
            <button onClick={colorChangerBlack}>Black</button>
            <button onClick={colorChangerGold}>GOLD</button>
            <button onClick={colorChangerMirror}>MIRROR</button>
            <button onClick={colorChangerRed}>RED</button>
<br/>
<br/>
            <button onClick={textChangerOtskaniryi}>otskaniryi</button>
            <button onClick={textChangerPodpiska10}>podpiska10</button>
            <button onClick={textChangerNavedite}>navedite</button>

            <br/>
            <br/>
            <input
                onChange={changeInstagramHandler}
                placeholder={'type text...'}
            />
            <div className='visitka'>
                <img className={'visitkaSvg'}
                     src={visitkaBorderColor}
                     ref={visitkaBorder}
                />


            </div>
            <div className={'otskaniryimenya'}>
                <img className={'otskaniryimenyaSVG'} src={footerText} />
            </div>
            <div className={"insatextDIV"}>

                <img className={'instagramSVG'}
                     src={instagramSVGColor}
                     // style={{color: 'red'}}
                     ref={instaLOGO_0}
                />
                <div className={'instagramTEXT'}
                     ref={instaTEXT_0}
                ><span className={textColor}

                >{instagram}</span></div>
            </div>
            {/*//////////////////////////////////////*/}
            <div className='visitka'>
                <img className={'visitkaSvg'}
                     src={nastoikeBorderColor}
                     ref={nastoikeBorder}
                />
            </div>

            <div className={"insatextDIV_forStand"} >

                <img className={'instagramSVG'}
                     src={instagramSVGColor}
                     // style={{color: 'red'}}
                     ref={instaLOGO}
                />
                <div className={'instagramTEXT'}
                     ref={instaTEXT}
                ><span className={textColor}

                >{instagram}</span></div>
            </div>

        </div>
    )
}